export class RIModel {

	constructor (){

		this.images = [];

	}

	get_images = () => {

		return this.images;
	}

	fetch_images = () => {

    if (process.env.NODE_ENV === "production") {
		return fetch("https://www.abbeyroad.org.uk/api/resident-involvment-page/get_all_images")
		.then((res)=>{
			return res.json()
		})
		.then((res)=>{

			if(res.success & res.data.length > 0){

				this.images = [...this.images,...res.data]

				return Promise.resolve({
					status: true,
					data: this.get_images()
				})
			}
			else{
				return Promise.reject({
					status: false
				})
				console.log("error loading images")
			}
		})
		.catch((err)=>{
			console.log(err)
			return Promise.reject({
					status: false
				})
		})
    }
    else{
		return fetch("http://localhost:3001/api/resident-involvment-page/get_all_images")
		.then((res)=>{
			return res.json()
		})
		.then((res)=>{

			if(res.success & res.data.length > 0){

				this.images = [...this.images,...res.data]

				return Promise.resolve({
					status: true,
					data: this.get_images()
				})
			}
			else{
				return Promise.reject({
					status: false
				})
				console.log("error loading images")
			}
		})
		.catch((err)=>{
			console.log(err)
			return Promise.reject({
					status: false
				})
		})
    }
	}

}